import server from '../../server.js';
export default {
	data() {
		return {
			sex:'男',
			user:[]
		}
	},
	onShow:function(){
		uni.request({
			method:'GET',
			url: 'https://api.angeli.top/user.php?type=getUserInfo', //仅为示例，并非真实接口地址。
			data: {
				auid: server.userinfo.Auid
			},
			header: {
				'content-type': 'application/x-www-form-urlencoded',
				'Cookie':server.cookie
			},
			success: (res) => {
				if(res.data.code=='1'){
					server.userinfo=res.data.data;
					this.user=res.data.data;
				}
			}
		});
	},
	methods: {
		toPage:function(type){
			uni.navigateTo({
				url: '../edit/edit?type='+type
			});
		},
		setsex:function(){
			uni.showActionSheet({
				itemList: ['男', '女'],
				success: function (res) {
					uni.request({
						method:'GET',
						url: 'https://api.angeli.top/user.php?type=setSex', //仅为示例，并非真实接口地址。
						data: {
							auid: server.userinfo.Auid,
							sex:res.tapIndex + 1
						},
						header: {
							'content-type': 'application/x-www-form-urlencoded',
							'Cookie':server.cookie
						},
						success: (res) => {
							if(res.data.code=='1'){
								uni.showToast({
									title: res.data.msg,
									position:'bottom',
									icon:'none'
								})
							}
						}
					});
				},
				fail: function (res) {
					uni.showToast({
						title: res.errMsg,
						position:'bottom',
						icon:'none'
					})
					console.log(res.errMsg);
				}
			});
		},
		uptouxiang:function(){
			uni.chooseImage({
				count: 1, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album','camera'], //从相册选择
			    success: (chooseImageRes) => {
			        const tempFilePaths = chooseImageRes.tempFilePaths;
			        uni.uploadFile({
			        	url: 'https://api.angeli.top/up/index.php', //仅为示例，非真实的接口地址
			        	filePath: tempFilePaths[0],
			        	name: 'file',
			        	formData: {
			        		'type': 'test'
			        	},
			        	header: {
			        		'Cookie':server.cookie
			        	},
			        	success: (uploadFileRes) => {
							console.log(uploadFileRes.data)
							if(uploadFileRes.data.code==1){
								this.user.AvatarUrl=uploadFileRes.data;
								uni.showToast({
									title: "修改成功！",
									position:'bottom',
									icon:'none'
								});
							}else{
								uni.showToast({
									title: uploadFileRes.data.msg,
									position:'bottom',
									icon:'none'
								});
							}
			        	},
			        	complete:function(){
			        		console.log("cuowu");
			        	}
			        });
			    },
				fail:function(){
					uni.showToast({
						title: "选择头像失败！",
						position:'bottom',
						icon:'none'
					});
				}
			});
			
			
			
		}
	}
}
